var tha;
import {
	mapState
} from 'vuex';
import common from '../../common.js';
import mInput from '../../components/m-input.vue'
export default {
	components: {
		mInput
	},
	onLoad(option) {
		tha = this;
		this.goType = option.backtype;
		this.successUrl = option.backpage;
	},
	onShow() {
		var icon = plus.nativeObj.View.getViewById("icon");
		if (icon) {
			icon.hide();
		}
	},
	onReady() {},
	data() {
		return {
			account: '',
			goType: 'switchTab',
			successUrl: "",
			password: ''
		};
	},
	computed: mapState(['forcedLogin', 'hasLogin', 'apiUrl']),
	methods: {
		...mapState(['login']),
		bindLogin() {
			if (this.account.length != 11) {
				uni.showToast({
					icon: 'none',
					title: '手机号不正确'
				});
				return;
			}
			if (this.password.length < 6) {
				uni.showToast({
					icon: 'none',
					title: '密码不正确'
				});
				return;
			}
			uni.setStorage({
				key: 'userInfo',
				data: {
					account: this.account,
					userName: 'test'
				},
				success: () => {
					console.log('save storage success!');
				}
			})
			this.toMain(this.account);
			/*uni.request({
				url: this.apiUrl + "/v1/login",
				method: 'POST',
				data: {
					mobile: this.account,
					password: this.password
				},
				success: (res) => {
					if (res.data.status == 200) {
						//登录成功
						uni.setStorage({
							key: 'userInfo',
							data: res.data.data.userInfo,
							success: () => {
								console.log('save storage success!');
							}
						})
						this.toMain(this.account)
					} else {
						uni.showToast({
							icon: 'none',
							title: '用户账号或密码不正确',
						});
					}
				}
			});*/
		},
		toMain(userName) {
			this.login(userName);
			if (this.goType && this.successUrl) {
				if (this.goType == "redirectTo") {
					uni.redirectTo({
						url: this.successUrl
					})
				} else {
					uni.switchTab({
						url: this.successUrl
					})
				}

			} else {
				uni.switchTab({
					url: '../index/map'
				});
			}
			var icon = plus.nativeObj.View.getViewById("icon");
			if (icon) {
				icon.show();
			}
		}
	}
}
